<template>
  <div v-if="!isMobile">
    <div class="centerTitle">
      <h1>Link</h1>
    </div>
    <v-container grid-list-md text-xs-center>
      <v-layout row wrap>
        <v-flex xs6 offset-xs3>
          <v-card flat hover>
            <h2 v-for="(item,index) in profiles" :key="index">
              {{item.title}}
              <p><a v-bind:href="item.url" target="_blank">{{item.value}}</a></p>
            </h2>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
  <div v-else>
    <div class="centerTitle">
      <h1>Link</h1>
    </div>
    <v-container grid-list-md text-xs-center>
      <v-layout row wrap>
        <v-flex xs12>
          <v-card flat hover>
            <h2 v-for="(item,index) in profiles" :key="index">
              {{item.title}}
              <p><a v-bind:href="item.url" target="_blank">{{item.value}}</a></p>
            </h2>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
export default {
  data: () => ({
    profiles: [
      { title: 'Twitter', value: '@WGG_SH', url: 'https://twitter.com/WGG_SH' },
      { title: 'Facebook', value: '安田直樹', url: 'https://www.facebook.com/naoki.yasuda.pgt' },
      { title: 'Qiita', value: '@WGG_SH', url: 'https://qiita.com/WGG_SH' },
      { title: 'Github', value: 'WGGSH', url: 'https://github.com/WGGSH' },
      { title: 'Bitbucket', value: 'WGGSH', url: 'https://bitbucket.org/WGGSH/' },
      { title: 'Blog', value: 'WGGの活動log', url: 'http://wgg.hatenablog.jp/' },
      { title: 'Youtube', value: 'WggSh', url: 'https://www.youtube.com/channel/UCasoRBlNMl3pYzcgLX6XLEg' },
      { title: 'NicoNico', value: 'WGG', url: 'https://www.nicovideo.jp/user/28727915' },
    ],
    history: [
      { title: 'プログラミング生放送主催 マスコットアプリ文化祭2015', value: ['株式会社ポケット・クエリーズ リズちゃん賞', '株式会社DMM.comラボ DMM.comラボ賞', '京都府精華町役場 京町セイカ賞'] },
      { title: 'マスコットアプリ文化祭2016', value: ['株式会社ポケット・クエリーズ 最先端技術賞', '株式会社ディー・エヌ・エー ハッカドール賞 - MMD利用部門', 'きみわたプロジェクト 君と私とせんばこき賞'] },
      { title: 'マスコットアプリ文化祭2017', value: ['株式会社ポケット・クエリーズ あいぼりぃたん賞', 'GMOインターネット株式会社 美雲あんず賞'] },
      { title: 'マスコットアプリ文化祭2018', value: ['GMOインターネット株式会社 このは&あんず賞'] },
      { title: '株式会社コロプラ主催 VRHACK祭 ', value: ['優勝 - 作品:清水DIVE'] },
      { title: '株式会社エイチーム ゲームプログラマインターン ', value: ['エイチームマインド賞'] },
      { title: '立命館大学主催 あいちゃれグローバル2017', value: ['ファイナリスト選出'] },
      { title: 'SECCON 2017 Online CTF', value: ['177位 (Team RiST)'] },
      { title: 'コミックマーケット C93', value: ['"初めてのHololens ~ MRアプリケーション開発" 執筆'] }
    ]
  }),
  computed: {
    isMobile () {
      const mobileBreakpoints = ['xs', 'sm']
      return mobileBreakpoints.some(e => {
        return this.$vuetify.breakpoint.name === e
      })
    }
  },
}
</script>

<style>

</style>



