<template>
  <div v-if="!isMobile">
    <!-- PC -->
    <div class="centerTitle">
    <h1>Contact</h1>
    <v-container grid-list-md text-xs-center>
      <v-layout row wrap>
        <v-flex xs4 offset-xs4>
          <v-card hover flat>
            <v-card-text>
              <a href="https://twitter.com/WGG_SH">Twitter(@WGG_SH)</a>か，naokipgt[at]gmail.comまで連絡ください
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
    </div>
  </div>
  <div v-else>
    <!-- Mobile -->
    <div class="centerTitle">
    <h1>Contact</h1>
    <v-container grid-list-md text-xs-center>
      <v-layout row wrap>
        <v-flex xs12>
          <v-card hover flat>
            <v-card-text>
              <a href="https://twitter.com/WGG_SH">Twitter(@WGG_SH)</a>か，naokipgt[at]gmail.comまで連絡ください
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    isMobile () {
      const mobileBreakpoints = ['xs', 'sm']
      return mobileBreakpoints.some(e => {
        return this.$vuetify.breakpoint.name === e
      })
    }
  }
}
</script>

<style>
</style>



