<template>
  <div v-if="!isMobile">
    <div class="centerTitle">
      <h1>WGG's Portfolio</h1>
        <v-container grid-list-md text-xs-center>
          <v-layout row wrap>
            <v-flex xs4 offset-xs4 v-for="(item,index) in menus" v-bind:key="index">
              <v-card hover flat v-bind:to="item.url" @click="currentPage='aaa'">
                <h1>{{item.title}}</h1>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
      </div>
    </div>
  <div v-else>
    <div class="centerTitle">
    <h1>WGG's Portfolio</h1>
      <v-container grid-list-md text-xs-center>
        <v-layout row wrap>
          <v-flex xs12  v-for="(item,index) in menus" v-bind:key="index">
            <v-card hover flat v-bind:to="item.url">
              <h1>{{item.title}}</h1>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </div>
  </div>
</template>

<script>
var currentPage;
export default {
  computed: {
    isMobile () {
      const mobileBreakpoints = ['xs', 'sm']
      return mobileBreakpoints.some(e => {
        return this.$vuetify.breakpoint.name === e
      })
    }
  },
  data: () => ({
    menus:[
      {title: 'Profile',url:'/profile'},
      {title: 'Work',url:'/work'},
      {title: 'Skill',url:'/skill'},
      {title: 'Link',url:'/link'},
      {title: 'Contact',url:'/contact'},
    ],
    currentPage: currentPage
  })
}
</script>


<style>

</style>



