<template>
  <v-app dark>
    <v-header>

      <v-toolbar app>
        <v-toolbar-title class="headline">
          <router-link to="/">
            <v-btn depressed large >
              <span class="font-weight-light">WGG's portfolio</span>
            </v-btn>
          </router-link>
        </v-toolbar-title>
      <v-spacer></v-spacer>
      <v-menu offset-y>
        <v-btn slot="activator" color="dark" dark>
          {{currentPage}}
          </v-btn>
          <v-list>
            <v-list-tile v-for="(item, index) in pages" :key="index" :to="item.url" @click="currentPage=item.title">
              <v-list-tile-title>{{ item.title }}</v-list-tile-title>
            </v-list-tile>
          </v-list>
        </v-menu>
      </v-toolbar>
    </v-header>

    <v-content>
       <router-view/>
      <!-- <Footer></Footer> -->
      <v-footer height="auto" color="lighten-0">
        <v-layout justify-center row wrap>
        <div>&copy; {{ new Date().getFullYear() }} — <strong>Yasuda Naoki / WGG</strong>
        </div>
        </v-layout>
      </v-footer>
    </v-content>
  </v-app>
</template>

<script>
import Header from './components/header.vue'
import Footer from './components/footer.vue'

var currentPage = 'Menu'

export default {
  name: 'App',
  components: {
    // HelloWorld
    Header, Footer
  },

  computed: {
    isMobile () {
      const mobileBreakpoints = ['xs', 'sm']
      return mobileBreakpoints.some(e => {
        return this.$vuetify.breakpoint.name === e
      })
    }
  },

  data: () => ({
    pages: [
      { title: 'Top', url: '/' },
      { title: 'Profile', url: '/profile' },
      { title: 'Work', url: '/work' },
      { title: 'Skill', url: '/skill' },
      { title: 'Link', url: '/link' },
      { title: 'Contact', url: '/contact' }
    ],
    currentPage: currentPage
  })
}
</script>

<style>
v-header{
  /* background: white; */
  position: sticky;
  top: 0px;
  z-index: 99;
}

a{
  text-decoration: none;
  color: #4d7ee7;
}
a:visited{
  color: #4d7ee7;
}

body{
  background: #212121;
}

h1{
  padding: 25px 00px;
  text-decoration: underline;
}
h2{
  padding: 3px 30px;
  font-size: 18px;
}
h2 p{
  padding: 0px 20px;
}
h2 li{
  margin: 0px 15px;
  font-size: 18px;
}
ul{
  padding-bottom: 10px;
  list-style: square;
}
@media screen and (max-width: 480px){
  h1{
    padding: 0px;
    text-decoration: underline;
  }
  h2{
    padding: 3px 30px;
    font-size: 14px;
  }
  h2 p{
    padding: 0px 20px;
    font-size: 13px;
  }
  h2 li{
    margin: 0px -10px;
    padding: 0px 0px;
    font-size: 13px;
  }
  ul{
    padding-bottom: 20px;
    list-style: none;
  }
}

.mycard{
  padding: 20px;
}


.centerTitle h1{
  text-align: center;
  text-decoration: none;
}


.v-card__title {
  background: gray;
  font-weight: 900;
  font-size: 24px;
}

.v-card{
  margin: 20px 20px;
  box-shadow: 5px 5px 5px 1px  black,-5px 5px 5px 1px  black;
}

.v-card__text p{
  padding: 0px;
  margin: 0px;
}

</style>



