<template>
  <div v-if="!isMobile">
    <div class="centerTitle">
      <h1>Skill</h1>
    </div>
    <v-container grid-list-md>
      <v-layout row wrap>
        <v-flex xs4 v-for="(item,index) in skills" v-bind:key="index">
          <v-card height=220>
            <v-card-title>
              {{item.title}}
            </v-card-title>
            <v-card-text>
              <!-- {{item.text}} -->
              <p v-for="(item2,index) in item.text" v-bind:key="index">
                {{item2}}
              </p>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
  <div v-else>
    <div class="centerTitle">
      <h1>Skill</h1>
    </div>
    <v-container grid-list-md>
      <v-layout row wrap>
        <v-flex xs12 v-for="(item,index) in skills" v-bind:key="index">
          <v-card>
            <v-card-title>
              {{item.title}}
            </v-card-title>
            <v-card-text>
              <!-- {{item.text}} -->
              <p v-for="(item2,index) in item.text" v-bind:key="index">
                {{item2}}
              </p>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
export default {
  data: () => ({
    skills: [
      { title: 'C/C++', text: ['・DxLib, OpenGLなどを使ったゲーム開発','・OpenCVなどを用いた画像処理','・Win32APIを用いたWindowsアプリ，ツールの開発','・競技プログラミング，CTF','などを行っています．'] },
      { title: 'C#', text: ['・WindowsFormsを使ったWindowsアプリケーションの開発','・Unityを使ったゲーム，アプリ開発の経験','・VR / MR開発','などを行った経験があります．'] },
      { title: 'Python', text: ['CTFやTensorflowを使った画像処理プログラムを開発するときに使いました．'] },
      { title: 'JavaScript (Node.js)', text: ['Node.jsでTwitterbotの開発などを行いました．'] },
      { title: 'Processing / P5.js', text: ['デジタルアート・インタラクティブアート制作や，ゲーム・モバイルアプリ開発に使用しています．'] },
      { title: 'Vue.js', text: ['このサイトを作るために勉強しました'] },
      { title: 'HTML / CSS', text: ['Webサイト制作に使用しています．'] },
      { title: 'Unity', text: ['ゲーム開発・xR系開発などを行っています．'] },
      { title: 'ShellScript / Powershell', text: ['簡易的な自動化スクリプトなどが必要なときに使っています．'] },
      { title: 'VR', text: ['Unityを用いてOculus RiftやGoogle Cardboard用のゲーム・アプリを開発した経験があります．'] },
      { title: 'MR', text: ['Unityを用いてHololensやMReal用のMRアプリ開発をした経験があります．'] },
      { title: 'AR', text: ['Unity, DxLib, OpenCV, ARToolKit, Vuforiaなどを使ってARアプリを作った経験があります．'] },
      { title: 'DR', text: ['DR: (Diminished Reality:隠消現実感 ) 物を消す技術で，大学の卒業研究ではこれについての研究・開発を行っていました．'] },
      { title: 'Kinect', text: ['Depthセンサを用いた3次元再構成，DRプログラムや，骨格センサを用いてVTuberシステムの開発を行った経験があります．'] },
      { title: 'Auto Hot Key', text: ['Windowsの作業効率向上の為にマクロ・ショートカットなどのスクリプトを作成しています．'] },
      { title: '3D Modeling', text: ['Blender, Metasequoiaなどを使って3Dモデリングを行っています．'] },
    ],
  }),
  computed: {
    isMobile() {
        const mobileBreakpoints = ["xs", "sm"];
        return mobileBreakpoints.some(e => {
          return this.$vuetify.breakpoint.name === e;
      });
    }
  },
}
</script>

<style>
</style>





