<template>
  <div v-if="!isMobile">
    <div class="centerTitle">
      <h1>Work</h1>
      <v-container grid-list-md>
        <v-layout row wrap>

          <v-flex xs4 v-for="(item,index) in works" v-bind:key="index">
            <v-card height=570>
              <v-card-title >
                {{item.title}}
              </v-card-title>
              <v-card-media :src="require('@/assets/'+item.image)" aspect-ratio="1.778" height="200" >
              </v-card-media>
              <v-card-text>
                <p v-for="(item2,index) in item.text" v-bind:key="index">
                  {{item2}}
                </p>
              </v-card-text>
              <v-card-text>
                <p>
                  使用言語・ツール: {{item.tool}}
                </p>
                <p>
                  開発期間: {{item.time1}}
                </p>
                <p>
                  開発時期: {{item.time2}}
                </p>
              </v-card-text>

            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </div>
  </div>
  <div v-else>
    <div class="centerTitle">
      <h1>Work</h1>
      <v-container grid-list-md>
        <v-layout row wrap>

          <v-flex xs12 v-for="(item,index) in works" v-bind:key="index">
            <v-card >
              <v-card-title >
                {{item.title}}
              </v-card-title>
              <v-card-media :src="require('@/assets/'+item.image)" aspect-ratio="1.778" height="200" >
              </v-card-media>
              <v-card-text>
                <p v-for="(item2,index) in item.text" v-bind:key="index">
                  {{item2}}
                </p>
              </v-card-text>
              <v-card-text>
                <p>
                  使用言語・ツール: {{item.tool}}
                </p>
                <p>
                  開発期間: {{item.time1}}
                </p>
                <p>
                  開発時期: {{item.time2}}
                </p>
              </v-card-text>

            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    isMobile () {
      const mobileBreakpoints = ['xs', 'sm']
      return mobileBreakpoints.some(e => {
        return this.$vuetify.breakpoint.name === e
      })
    }
  },
  data: () => ({
    works: [
      { title: 'Tetris Dim.4', image: 'tetris.jpg', text: ['テトリスのゲームフィールドを4次元空間にしてみた高難易度パズルゲームです．デモンストレーション用に自動プレイするAIも作成しました'], tool: 'C++, DxLib', time1: '19日', time2: '2016年4月'},
      { title: 'MMD × AR', image: 'mmdar.jpg', text: ['所属サークルのクリスマス会に向けて作成した，クリスマスの夜をディスプレイの中の女の子たちと一緒に過ごす作品です．自作のARマーカ検出器を用いて，マーカ上にMMDモデルが表示されます．','マスコットアプリ文化祭2015で，「リズちゃん賞」及び，「DMM.comラボ賞」を受賞しました'] ,tool: 'C/C++, DxLib, OpenCV', time1: '約2週間', time2: '2015年12月'},
      { title: 'ARによる空中ディスプレイ', image: 'ardisplay.jpg', text: ['スマートフォンからディスプレイが立体に浮き上がるとカッコいいと思って,ただそれだけで作った作品です.上のMMD×ARと同じ自作のマーカ検出を利用しています. ','マスコットアプリ文化祭2015で京町セイカ賞を受賞しました. '] ,tool: 'C/C++, DxLib, OpenCV',time1: 'MMD × AR の制作後に追加で2日', time2: '2016年1月'},
      { title: '透明マント', image: 'transdisplay.jpg', text: ['所属サークルの学園祭に向けて作成した作品です.学園祭では,大きなタオルを使用して自分自身や来場者さんの姿を消したことで盛り上がりました.指定した色と近い色の部分を事前に撮影した背景に変化させています. '] ,tool: 'C, OpenCV', time1: '約3週間', time2: '2015年10月'},
      { title: '清水 DIVE VR', image: 'kiyomizu.jpg', text: ['サークルメンバーとVRハッカソンで制作した作品です. VRで「人工恐怖」の表現を目指しました.ハコスコなどのスマホ用HMDを使用することで,お手軽に「落下の恐怖」を体験できるスマホアプリです.自分は主に清水寺のモデリングを担当し,他のメンバーにUnityでのシステム作成や,送風機のハード制御を行ってもらいました. ','株式会社コロプラ様が主催した「VRHack祭 in Kyoto」で優勝しました. '] ,tool: 'C#, Unity, Blender など', time1: '約2週間', time2: '2016年3月'},
      { title: '4/5次元超立方体の可視化', image: 'dim.jpg', text: ['東洋大学の研究記事を読んで,任意の次元の超立方体を可視化してみようと思い,作った作品です '] ,tool: 'Processing', time1: '約1週間', time2: '2016年5月'},
      { title: 'Mascot Shooter AR', image: 'mascotar.jpg', text: ['好きなキャラクターを現実世界に呼び出して戦えたら面白いだろうなーっという思いで,昨年作ったARマーカ検出器を改良して,それを用いて遊ぶゲームを作りました. ','マスコットアプリ文化祭2016で「最先端技術賞」「ハッカドール賞 - MMD利用部門」「君と私とせんばこき賞」の3賞を同時受賞しました. '] ,tool: 'C++, DxLib, OpenCV', time1: '約3週間', time2: '2016年12月'},
      { title: 'ProcessingMery', image: 'processingmery.jpg', text: ['テキストエディタ「Mery」でProcessingのコーディング及び実行をするための拡張機能です.シンタックスハイライト,インテリセンス,ビルド及び実行機能を利用できます'] ,tool: '特になし', time1: '2日', time2: '2017年5月'},
      { title: 'MascotOmniShooter', image: 'mascotomnishooter.jpg', text: ['「FPSゲームを2Dにするとどうなるか」をコンセプトに作成した,「全方位シューティングゲーム」です．「フィールドを回す」ことで狙いの敵を倒します．3人のキャラを上手く切り替えて全滅しないように目指します．','マスコットアプリ文化祭2017で「あいぼりぃたん賞」「美雲あんず賞」のダブル受賞をしました'] ,tool: 'C#, Unity', time1: '約2週間', time2: '2017年12月'},
      { title: 'マスコットスライドパズル', image: 'slidepuzzle.jpg', text: ['ブラウザで遊べる15パズルです．難易度を変更して最大624パズルににまでできます．ブラウザで動作しますが，スマートフォンでも快適に遊べるように工夫しています．','マスコットアプリ文化祭2018で「このは&あんず賞」を受賞しました']
      ,tool: 'p5.js, typescript', time1: '約1週間', time2: '2018年9月 + 2018年12月'},
      { title: 'DRプログラム', image: 'dr.jpg', text: ['大学の卒業研究で開発していたDRプログラムです．本来人が写っているところを，存在していないように見せたりします．'] ,tool: 'C++, Python, OpenCV, Tensorflowなど', time1: '約2週間', time2: '2018年11月'},
      { title: 'And more...', image: 'more.png', text: ['これからも追加する予定です'] ,tool: 'わからん', time1: 'わからん', time2: '2019年〜?'}
    ]
  })
}
</script>

<style>
.v-card__title{
  background: gray;
  font-weight: 900;
  font-size: 24px;
  box-shadow: none;
}

</style>



